.no-webp .imagen-contacto{
    background-image: url('../../../bienesraices/build/img/encuentra.jpg');
}

.webp .imagen-contacto{
    background-image: url('../../../bienesraices/build/img/encuentra.webp');
}

.imagen-contacto{
    background-position: center center;
    background-size: cover;
    padding: $separacion 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    h2{
        color: $blanco;
    }
    p{
        color: $blanco;
        font-weight: bold;
    }
}