  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

/** UI **/
.no-webp .inicio {
  background-image: url(../../../bienesraices/build/img/header.jpg);
}

.webp .inicio {
  background-image: url(../../../bienesraices/build/img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 50rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header.hinicio h1 {
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .contenido-header.hinicio {
    min-height: 50rem;
  }
  .contenido-header.hinicio h1 {
    text-align: left;
    font-weight: bold;
    font-size: 3.5rem;
  }
}
.contenido-header h1 {
  text-align: left;
  font-weight: bold;
  font-size: 2rem;
}

.footer {
  background-color: #333333;
  padding: 5rem/2 0;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 5rem/2;
}
.footer p {
  text-align: center;
}

.derecha {
  margin: 0 auto;
  width: 4rem;
}
.derecha .boton-dark {
  filter: invert(100%);
}
.derecha .boton-dark:hover {
  cursor: pointer;
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.5s ease-in-out, opacity 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    visibility: visible;
    opacity: 1;
    display: flex;
    align-items: center;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 4rem;
  margin: 0 auto;
}

.mostrar-nav {
  visibility: visible;
  opacity: 1;
}

.navegacion a:hover {
  color: #71B100;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.iconos-nosotros .icono img {
  height: 10rem;
}
.iconos-nosotros .icono h3 {
  font-size: 3rem;
  text-align: center;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    margin: 2rem 7rem;
  }
}
.anuncio {
  background-color: #e1e1e1;
  border: 2px solid #c8c8c8;
  margin-bottom: 3rem;
}
.anuncio .precio {
  color: green;
  font-weight: 900;
  font-size: 3rem;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0 4rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
}
.iconos-caracteristicas li p {
  text-align: center;
  margin: 0;
  font-weight: bold;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 0;
  display: block;
  border: none;
  width: 100%;
}
.boton-amarillo:hover {
  background-color: #af6907;
}

.boton-amarillo-ib {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 0;
  display: inline-block;
  border: none;
}
.boton-amarillo-ib:hover {
  background-color: #af6907;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 0;
  display: inline-block;
  border: none;
}
.boton-verde:hover {
  background-color: #507e00;
}

.boton-rojo-block {
  background-color: red;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 0;
  display: block;
  border: none;
  width: 100%;
}
.boton-rojo-block:hover {
  background-color: #cc0000;
}

.boton-azul-block {
  background-color: darkblue;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 0;
  display: block;
  border: none;
  margin-top: 1rem;
}
.boton-azul-block:hover {
  background-color: #000058;
}

.no-webp .imagen-contacto {
  background-image: url("../../../bienesraices/build/img/encuentra.jpg");
}

.webp .imagen-contacto {
  background-image: url("../../../bienesraices/build/img/encuentra.webp");
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  color: #FFFFFF;
}
.imagen-contacto p {
  color: #FFFFFF;
  font-weight: bold;
}

@media (min-width: 768px) {
  .blog-testimoniales {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}

.texto-entrada a {
  color: black;
}
.texto-entrada h4 {
  font-weight: 900;
  margin: 0;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 14rem;
  height: 0.5rem;
  background-color: #71B100;
}
.texto-entrada span {
  font-weight: 900;
  color: #E08709;
}

.contenedor-testimoniales {
  background-color: #71B100;
  padding: 1rem 2rem;
  border-radius: 3%;
}
.contenedor-testimoniales p {
  color: #FFFFFF;
}
.contenedor-testimoniales span {
  font-size: 5rem;
  color: #FFFFFF;
}
.contenedor-testimoniales .autor {
  font-weight: bold;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .contenedor-nosotros {
    display: grid;
    gap: 1;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
  .contenedor-nosotros span {
    font-weight: bold;
  }
}
.iconos-anuncio {
  width: 300px;
  margin: 0 auto;
}

.resumen-propiedad .precio {
  text-align: center;
  color: #71B100;
  font-weight: 900;
}
@media (min-width: 768px) {
  .resumen-propiedad .precio {
    font-size: 5rem;
  }
}
.resumen-propiedad .descripcion {
  padding: 3rem;
  text-align: center;
}
.resumen-propiedad span {
  font-weight: bold;
}

.imagen-unica {
  width: 75%;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .contenedor-blog {
    margin: 6.5rem;
  }
}
.contenedor-blog .entrada-blog {
  margin-bottom: 4rem;
}

.copyright {
  color: #FFFFFF;
  margin-bottom: 0;
}

.formulario p {
  font-size: 1.5rem;
}
.formulario legend {
  font-size: 2rem;
  font-weight: bold;
}
.formulario label {
  font-weight: bold;
  text-transform: uppercase;
  display: block;
  margin-top: 2rem;
}
.formulario input:not([type=submit]), .formulario textarea, .formulario select {
  width: 90%;
  padding: 1rem;
  border: 2px solid #e1e1e1;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .opcion-contacto {
  display: flex;
  max-width: 25rem;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}
.formulario .field-contacto {
  margin-bottom: 2rem;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode h1 {
  color: #e1e1e1;
}
.dark-mode .anuncio {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .anuncio .precio {
  color: #71B100;
}
.dark-mode .anuncio li img {
  filter: invert(100%);
}
.dark-mode .iconos-anuncio li img {
  filter: invert(100%);
}
.dark-mode .formulario legend {
  color: #e1e1e1;
}
.dark-mode .formulario label {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]), .dark-mode .formulario textarea, .dark-mode .formulario select {
  background-color: #333333;
  color: #FFFFFF;
}
.dark-mode td {
  color: white;
  font-weight: bold;
}

.alerta p {
  color: white;
  margin-bottom: 1rem;
  text-align: center;
  padding: 1rem 2rem;
  background-color: red;
}
.alerta.error {
  background-color: red;
}

.notificacion {
  color: white;
  text-align: center;
  padding: 0;
  background-color: darkblue;
}

.exito {
  color: white;
  text-align: center;
  padding: 0;
  background-color: green;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #71B100;
}
table.propiedades thead th {
  color: white;
}
table.propiedades tr td {
  font-weight: bold;
  text-align: center;
}
table.propiedades tr td img {
  margin: 0 auto;
}

.imagen-propiedad {
  width: 25rem;
}

.imagen-update {
  width: 30rem;
  margin: 0 auto;
}

.tabla-vendedores {
  margin: 0 auto;
  width: 100%;
  border-spacing: 0;
}
.tabla-vendedores td {
  text-align: center;
  padding: 1rem 2rem;
}
.tabla-vendedores thead {
  background-color: #71B100;
}

.error {
  color: white;
  text-align: center;
  padding: 0;
  background-color: darkred;
}

.formulario-centrado {
  max-width: 45rem;
  margin: 0 auto;
}
.formulario-centrado .boton-verde {
  width: 100%;
  margin-top: 2rem;
}